body {
  margin: 0;
  color: white;
  background-color: black;
  font-family: 'Raleway', sans-serif;
}
a {
  text-decoration: none;
  transition: 0.5s;
  &:visited {
    text-decoration: none;
  }
}
video {
  display: block;
}
#video {
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  -ms-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  background: black;
  background-size: cover;
}
/* --------.container-------- */
.container {
  margin-bottom: 100px;
}
/* --------banner-------- */
#banner_outer {
  margin-top: 100px;
  margin-bottom: 10px;
}
#banner_inner {
  display: table;
  margin: 0 auto;
}
#banner_text {
  padding: 50px;
  text-transform: uppercase;
  font-size: 80px;
  font-weight: 400;
  text-align: center;
  border: 2px solid rgba(255, 255, 255, 0.76);
  color: white;
  background-color: rgba(255, 255, 255, 0.17);
  text-shadow: 0 0 20px black;
}
/* --------Forms-------- */
.form-group {
  input,
  textarea {
    border-radius: 0;
    color: white;
    background-color: rgba(0, 0, 0, 0.47);
    border-color: rgba(152, 152, 152, 0.47);
    &:focus {
      outline-color: rgba(0, 0, 0, 0.47);
      border-color: rgba(152, 152, 152, 0.84);
    }
  }
}
/* --------Buttons-------- */
.button {
  color: white;
  background-color: rgba(0, 0, 0, 0.47);
  border-color: rgba(152, 152, 152, 0.47);
  &,
  &:focus {
    padding: 5px 15px !important;
    outline-color: rgba(0, 0, 0, 0.47);
    border: 1px solid rgba(152, 152, 152, 0.47) !important;
  }
  &:hover {
    border-color: rgba(152, 152, 152, 0.84) !important;
    color: rgb(223, 223, 223) !important;
  }
  &:active {
    color: rgb(198, 198, 198) !important;
    border: 1px solid rgba(152, 152, 152, 0.47) !important;
  }
}
/* --------Map-------- */
#map {
  iframe {
    width: 100%;
    height: 30em;
    border: 1px solid rgba(152, 152, 152, 0.47);
    pointer-events: none;
  }
}
/* -------- Styling modal dialogs -------- */
.modal-content {
  background: rgb(54, 54, 54);
  border-radius: 0;
}
.modal-backdrop {
  opacity: 0.9 !important;
}
/* --------.about section-------- */
.about {
  background-color: rgba(0, 0, 0, 0.83);
  padding: 10px;
  figure {
    height: 200px;
    overflow: hidden;
  }
  .about-description {
    padding-top: 10px;
    letter-spacing: 1px;
    font-size: 1em;
  }
}
/* --------Sign-------- */
#sign_outer {
  margin-top: 25px;
}
table#sign_inner {
  margin: 0 auto;
  border-collapse: separate;
  border-spacing: 0;
  td {
    height: 100%;
    border: 2px solid transparent;
    transition: 0.2s;
    &:hover {
      border: 2px solid rgba(255, 255, 255, 0.65);
      transition: 0.2s;
    }
    a:focus,
    a:hover {
      color: white;
      text-decoration: none;
    }
    a {
      display: block;
      padding: 30px;
      font-size: 30px;
      text-transform: uppercase;
      color: white;
    }
  }
}
#log_in_box {
  border-right: 1px solid white !important;
}
#sign_up_box {
  border-left: 1px solid white;
}
#log_in_box,
#sign_up_box {
  width: 50%;
  font-size: 15px !important;
  text-align: center;
}
/* --------Helper classes-------- */
.darken {
  background-color: rgba(0, 0, 0, 0.4);
}
.ulVisible {
  height: 100%;
}
/*-------ABOUT-------*/
.header-image {
  display: block;
  width: 100%;
  text-align: center;
  background: 'imgs/about/minerals.jpg' no-repeat center center scroll;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  background-size: cover;
  -o-background-size: cover;
}
.headline h1 {
  font-size: 130px;
}
.headline h2 {
  font-size: 77px;
}
.featurette-divider {
  margin: 80px 0;
}
.featurette {
  overflow: hidden;
}
.featurette-image.pull-left {
  margin-right: 40px;
}
.featurette-image.pull-right {
  margin-left: 40px;
}
.featurette-heading {
  font-size: 50px;
}
/*----textarea----*/
.red {
  color: rgb(226, 120, 43);
}
/*---------CONTACT -------*/
.center {
  text-align: center;
  padding-bottom: 55px;
}
.gmap-area {
  background-image: url("../imgs/map.jpg");
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 70px 0;
}
.gmap-area .gmap iframe {
  border-radius: 50%;
  height: 280px;
  width: 300px;
}
.gmap {
  border: 10px solid #f6f5f0;
  border-radius: 50%;
  height: 300px;
  width: 300px;
  box-shadow: 0 0 3px 3px #e7e4dc;
  overflow: hidden;
  position: relative;
  margin: 0 auto;
}
.gmap-area .map-content ul {
  list-style: none;
}
.gmap-area .map-content address h5 {
  font-weight: 700;
  color: black;
  /* font-size: 16px; */
}
.gmap-area .map-content address {
  color: grey;
}
#contact-page {
  padding-top: 0;
}
#contact-page .contact-form .form-group label {
  font-size: 16px;
  font-weight: 300;
}
.form-group .form-control {
  padding: 7px 12px;
  border-color: #f2f2f2;
  box-shadow: none;
}
textarea#message {
  resize: none;
  padding: 10px;
}
#contact-page .contact-wrap {
  margin-top: 20px;
}
/* --------product page-------- */
/*--------inc header ------*/
#header {
  height: 120px;
}
#header .search {
  display: inline-block;
}
.navbar>.container .navbar-brand {
  margin-left: 0;
}
.top-bar {
  height: 100px;
  padding: 10px 0;
  background: black;
  border-bottom: 1px solid #222;
  line-height: 28px;
}
.top-number {
  color: #fff;
}
.top-number p {
  margin: 0;
}
.social {
  text-align: right;
}
.social-share {
  display: inline-block;
  list-style: none;
  padding: 0;
  margin: 0;
}
ul.social-share li {
  display: inline-block;
}
ul.social-share li a {
  display: inline-block;
  color: #fff;
  background: #404040;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  border-radius: 2px;
}
ul.social-share li a:hover {
  background: #c52d2f;
  color: #fff;
}
a.addProduct {
  padding: 0.6em 1em;
  color: #fff;
  background-color: #404040;
  border-radius: 2px;
}
a.addProduct:hover {
  text-decoration: none;
  background: #c52d2f;
  color: #fff;
}
.search i {
  color: #fff;
}
input.search-form {
  background: transparent;
  border: 0 none;
  width: 60px;
  padding: 0 10px;
  color: #FFF;
  -webkit-transition: all 400ms;
  -moz-transition: all 400ms;
  -ms-transition: all 400ms;
  -o-transition: all 400ms;
  transition: all 400ms;
}
input.search-form::-webkit-input-placeholder {
  color: transparent;
}
input.search-form:hover::-webkit-input-placeholder {
  color: #fff;
}
input.search-form:hover {
  width: 180px;
}
.navbar-brand {
  padding: 0;
  margin: 0;
}
.navbar {
  border-radius: 0;
  margin-bottom: 0;
  background: #151515;
  padding: 15px 0 0;
}
.navbar-nav {
  margin-top: 12px;
}
.navbar-nav>li {
  margin-left: 35px;
  padding-bottom: 28px;
}
.navbar-inverse .navbar-nav > li > a {
  padding: 5px 12px;
  margin: 0;
  border-radius: 3px;
  color: #fff;
  line-height: 24px;
  display: inline-block;
}
.navbar-inverse .navbar-nav > li > a:hover {
  background-color: #c52d2f;
  color: #fff;
}
.navbar-inverse {
  border: none;
}
.navbar-inverse .navbar-brand {
  font-size: 36px;
  line-height: 50px;
  color: #fff;
}
.navbar-inverse .navbar-nav > .active > a,
.navbar-inverse .navbar-nav > .active > a:hover,
.navbar-inverse .navbar-nav > .active > a:focus,
.navbar-inverse .navbar-nav > .open > a,
.navbar-inverse .navbar-nav > .open > a:hover,
.navbar-inverse .navbar-nav > .open > a:focus {
  background-color: #c52d2f;
  color: #fff;
}
.navbar-inverse .navbar-nav .dropdown-menu {
  background-color: rgba(0,0,0,.85);
  -webkit-box-shadow: 0 3px 8px rgba(0, 0, 0, 0.125);
  -moz-box-shadow: 0 3px 8px rgba(0, 0, 0, 0.125);
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.125);
  border: 0;
  padding: 0;
  margin-top: 0;
  border-top: 0;
  border-radius: 0;
  left: 0;
}
.navbar-inverse .navbar-nav .dropdown-menu:before {
  position: absolute;
  top: 0;
}
.navbar-inverse .navbar-nav .dropdown-menu > li > a {
  padding: 8px 15px;
  color: #fff;
}
.navbar-inverse .navbar-nav .dropdown-menu > li:hover > a,
.navbar-inverse .navbar-nav .dropdown-menu > li:focus > a,
.navbar-inverse .navbar-nav .dropdown-menu > li.active > a {
  background-color: #c52d2f;
  color: #fff;
}
.navbar-inverse .navbar-nav .dropdown-menu > li:last-child > a {
  border-radius: 0 0 3px 3px;
}
.navbar-inverse .navbar-nav .dropdown-menu > li.divider {
  background-color: transparent;
}
.navbar-inverse .navbar-collapse,
.navbar-inverse .navbar-form {
  border-top: 0;
  padding-bottom: 0;
}
/*----------Footer-----*/
#bottom {
  background: black;
  border-top: 1px solid #222;
  font-size: 14px;
}
#bottom h3 {
  padding-top: 20px;
  margin-top: 0;
  margin-bottom: 10px;
  text-transform: uppercase;
  font-size: 22px;
}
#bottom ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
#bottom ul li {
  display: block;
  padding: 5px 0;
}
#bottom ul li a {
  color: #808080;
}
#bottom ul li a:hover {
  color: #c52d2f;
}
#bottom .widget {
  margin-bottom: 0;
}
/* --------footer-------- */
footer {
  background-color: rgba(0, 0, 0, 0.9);
  position: fixed;
  text-align: center;
  bottom: 0;
  width: 100%;
  ul {
    height: auto;
  }
  ol,
  ul {
    margin-top: 0;
    margin-bottom: 0;
  }
  .glyphicon {
    display: none;
    color: rgba(152, 152, 152, 0.84);
    font-size: 2em;
    &:hover {
      color: white;
      cursor: pointer;
    }
  }
}
nav {
  li {
    margin: 0;
    display: inline-block;
    text-transform: uppercase;
    width: 15%;
    &:hover {
      // background-color:rgba(152, 152, 152, 0.84);
      text-decoration: none !important;
    }
    a {
      display: inline-block;
      padding-top: 10px;
      padding-bottom: 10px;
      font-size: 13px;
      color: antiquewhite;
      &:link {
        text-decoration: none !important;
      }
      &:hover {
        color: inherit !important;
        text-shadow: 0 0 10px white !important;
      }
    }
    &:hover {
      text-shadow: 0 0 10px white;
      text-decoration: underline;
      transition: 0.15s;
    }
  }
}
/*------thumbnail-----*/
.thumbnail {
  border-radius: 0;
  padding: 7px;
  background-color: rgba(0, 0, 0, 0.85);
  height: 500px;
  width: 262px;
  overflow: auto;
  .btn-group-justified {
    bottom: 35px;
    position: absolute;
    width: 200px;
    left: 47px;
  }
  .countdown {
    top:10px;
    position: absolute;
  }
  .caption {
    color: white !important;
  }
  .product-name {
    font-weight: bold;
    a {
      &:link,
      &:visited {
        color: white;
      }
    }
  }
}
.nothing {
  margin-left: 5px;
}
.product-image {
  height: 0;
  padding-bottom: 100%;
  overflow-y: hidden;
  img {
    width: 100%;
  }
}
/*----------BID-------*/
/*-----CHECKOUT-------*/
table#checkout td {
  border-top: none !important;
  .thumbnail {
    height: auto;
    width: auto;
  }
}
/*---------404----------*/
.error {
  margin: 0 auto;
  text-align: center;
}
.error-code {
  bottom: 60%;
  color: #2d353c;
  font-size: 96px;
  line-height: 100px;
}
.error-desc {
  font-size: 25px;
  color: #647788;
}
.m-b-10 {
  margin-bottom: 10px !important;
}
.m-b-20 {
  margin-bottom: 20px !important;
}
.m-t-20 {
  margin-top: 20px !important;
}
/*-----CART------*/
#cart {
  padding: 0;
}
#cart .empty {
  font-style: italic;
  color: #a0a3ab;
  font-size: 14px;
  letter-spacing: 1px;
}
.badge {
  padding-left: 9px;
  padding-right: 9px;
  -webkit-border-radius: 9px;
  -moz-border-radius: 9px;
  border-radius: 9px;
}
#cart-badge {
  font-size: 14px;
  background: #696969;
  color: #fff;
  padding: 0 5px;
  vertical-align: top;
}
/*--------CreateBid------*/
#createBid {
  select {
    color: black;
  }
}
/*---------FIX----------*/
.alert-danger {
  padding-left: 100px;
}
@media only screen and (min-width: 480px) {
  footer ul {
    display: block !important;
  }
}
ul,
menu,
dir {
  -webkit-padding-start: 0;
}
/* --------Media queries-------- */
@media only screen and (max-width: 700px) {
  body {
    background: url('/imgs/stars.jpg');
  }
  video {
    display: none;
  }
  #banner_inner {
    display: block;
  }
  #banner_text {
    font-size: 2.8em;
    padding: 20px;
  }
  footer {
    .glyphicon {
      display: inline-block;
    }
    ul {
      height: 0;
      padding: 0;
      transition: all 0.2s ease-in;
    }
    li {
      display: block !important;
      text-align: center;
      margin: 0 auto !important;
      width: 100% !important;
    }
    li.welcome {
      display: none !important;
    }
    a {
      font-size: 30px !important;
    }
  }
}
@media(max-width:1200px) {
  .headline h1 {
    font-size: 140px;
  }
  .headline h2 {
    font-size: 63px;
  }
  .featurette-divider {
    margin: 50px 0;
  }
  .featurette-image.pull-left {
    margin-right: 20px;
  }
  .featurette-image.pull-right {
    margin-left: 20px;
  }
  .featurette-heading {
    font-size: 35px;
  }
}
@media(max-width:991px) {
  .headline h1 {
    font-size: 105px;
  }
  .headline h2 {
    font-size: 50px;
  }
  .featurette-divider {
    margin: 40px 0;
  }
  .featurette-image {
    max-width: 50%;
  }
  .featurette-image.pull-left {
    margin-right: 10px;
  }
  .featurette-image.pull-right {
    margin-left: 10px;
  }
  .featurette-heading {
    font-size: 30px;
  }
}
@media(max-width:768px) {
  .container {
    margin: 0 15px;
  }
  .featurette-divider {
    margin: 40px 0;
  }
  .featurette-heading {
    font-size: 25px;
  }
}
@media(max-width:668px) {
  .headline h1 {
    font-size: 70px;
  }
  .headline h2 {
    font-size: 32px;
  }
  .featurette-divider {
    margin: 30px 0;
  }
}
@media(max-width:640px) {
  .headline {
    padding: 75px 0 25px;
  }
  .headline h1 {
    font-size: 60px;
  }
  .headline h2 {
    font-size: 30px;
  }
}
@media(max-width:375px) {
  .featurette-divider {
    margin: 10px 0;
  }
  .featurette-image {
    max-width: 100%;
  }
  .featurette-image.pull-left {
    margin-right: 0;
    margin-bottom: 10px;
  }
  .featurette-image.pull-right {
    margin-bottom: 10px;
    margin-left: 0;
  }
}
